___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary-color:rgb(13, 106, 136);
    --primary-color-darker:rgb(9, 48, 56);
}
*{
    box-sizing: border-box;
    outline: 0;
}
body{
    margin: 0;
    padding: 0;
    background-color: var(--primary-color);
    font-family: 'Open sans', sans-serif;
    font-size: 1.3em;
    line-height: 1.5em;
}
.container{
    max-width: 640px;
    margin: 50px auto;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
}
form input, form label, form button{
    display: block;
    width: 100%;
    margin-bottom: 10px;
}
form input{
    font-size: 24px;
    height: 50px;
    padding: 0 20px;
}
form input:focus{
    outline: 1px solid var(--primary-color);
}
form button{
    border: none;
    background-color: var(--primary-color);
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    height: 50px;
    cursor: pointer;
    margin-top: 30px;
}
form button:hover{
    background-color: var(--primary-color-darker);
}
section div{
    margin-bottom: 20px;
    font-size: 1.5rem;
    text-align: center;
    font-weight: bold;
    letter-spacing: 5px;
}
.container h1 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 40px;
}
